<template>
  <div class="identity bgfff">
    <h2 class="title">任务管理</h2>
    <!-- 查询 -->
    <div class="search-options">
      <span class="status">
        任务状态：
        <el-select v-model="authStatus.value" placeholder="请选择">
          <el-option
            v-for="item in authStatus.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </span>
      <span class="company">
        合作方：
        <el-select v-model="authCompany.value" placeholder="请选择">
          <el-option
            v-for="item in authCompany.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </span>
      <span>
        <el-button class="short-button" @click="getTaskList()">查询</el-button>
        <el-button class="normal-button short-button" @click="reset()">重置</el-button>
      </span>
      <span class="handler">
        <el-dropdown placement="bottom" @command="auth">
          <el-button class="normal-button long-button">
            批量操作
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" class="handler-button">
            <el-dropdown-item command="accepted">同意</el-dropdown-item>
            <el-dropdown-item command="rejected">拒绝</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button class="long-button" @click="toStart">发起计算</el-button>
      </span>
    </div>

    <!-- 表格 -->
    <el-table
      header-row-class-name="table-header"
      row-class-name="table-tr"
      :data="tableData"
      @selection-change="handleSelectionChange"
      border
      :cell-class-name="getClassName"
      height="400"
      style="width: 100%"
    >
      <el-table-column align="center" type="selection" width="55" :selectable="isSelectable"></el-table-column>
      <el-table-column
        align="center"
        show-overflow-tooltip
        v-for="column in columns"
        :key="column.id"
        :prop="column.id"
        :label="column.name"
      ></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <span
            :class="{'identify-disable':!scope.row.canOperate}"
            class="identify-pass"
            v-if="scope.row.status==='waiting'"
            @click="handlerTask(scope.row.taskId,'accepted')"
          >同意</span>
          <span
            :class="{'identify-disable':!scope.row.canOperate}"
            class="identify-pass"
            @click="handlerTask(scope.row.taskId,'rejected')"
            v-if="scope.row.status==='waiting'"
          >拒绝</span>
          <span class="identify-pass" @click="viewDetail(scope.row)">详情</span>
        </template>
      </el-table-column>
    </el-table>
    <div class="identity-pagination">
      <span>共{{total}}条记录</span>
      <span class="right-part">
        <span style="margin-right:10px">共{{totalPage}}页</span>
        <el-select
          v-model="pageSizeList.value"
          @change="handleSizeChange"
          class="page-size-select"
          placeholder="请选择"
        >
          <el-option
            v-for="item in pageSizeList.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="size"
          layout="prev, pager, next"
          :total="total"
        ></el-pagination>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
      total: 0,
      totalPage: 0,
      tableData: [],
      multipleSelection: [],
      columns: [
        {
          id: "status_fmt",
          name: "认证状态",
        },
        {
          id: "taskId",
          name: "任务编号",
        },
        {
          id: "taskPort",
          name: "计算端口",
        },
        {
          id: "clientName",
          name: "发起方",
        },
        {
          id: "serverName",
          name: "合作方",
        },
        {
          id: "resultFile",
          name: "任务输出",
        },
      ],
      pageSizeList: {
        value: 30,
        options: [
          {
            value: 10,
            label: "每页10条",
          },
          {
            value: 30,
            label: "每页30条",
          },
          {
            value: 50,
            label: "每页50条",
          },
        ],
      },
      authStatus: {
        value: "",
        options: [
          {
            value: "waiting",
            label: "等待加入",
          },
          {
            value: "accepted",
            label: "计算中",
          },
          {
            value: "finished",
            label: "成功",
          },
          {
            value: "rejected",
            label: "已拒绝",
          },
        ],
      },
      authCompany: {
        value: "",
        options: [],
      },
      rules: {
        ip: [
          {
            validator: this.Utils.checkIp.bind(this),
            trigger: ["change", "blur"],
          },
        ],
        port: [{ type: "number", message: "请输入正确的端口" }],
      },
      form: {
        ip: "",
        port: "",
      },
      size: 30,
    };
  },
  methods: {
    viewDetail(row) {
      this.$router.push({
        name: "detail",
        params: {
          taskId: row.taskId,
        },
      });
    },
    toStart() {
      this.$router.push({
        path: "/task/start",
      });
    },
    getClassName({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 1) {
        if (row.status === "finished") {
          return "status-finished";
        } else if (row.status === "accepted") {
          return "status-accepted";
        } else if (row.status === "rejected"||row.status === "abort") {
          return "status-rejected";
        } else {
          return "status-waiting";
        }
      }
    },
    reset() {
      this.authStatus.value = "";
      this.authCompany.value = "";
      this.getTaskList();
    },
    isSelectable(row, index) {
      return row.canOperate && row.status === "waiting" ? true : false;
    },
    auth(type) {
      let ids = [];
      let isValidate = true;
      if (this.multipleSelection.length === 0) {
        return;
      }
      this.multipleSelection.forEach((item) => {
        ids.push(item.taskId);
        if (!item.canOperate) {
          isValidate = false;
        }
      });
      let idStr = ids.join(",");
      if (isValidate) {
        this.handlerTask(idStr, type);
      } else {
        this.Utils.message("无操作权限！", "warning");
      }
    },
    handlerTask(id, type) {
      let mes = type === "accepted" ? "是否同意？" : "是否拒绝？";
      this.$confirm(mes, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        cancelButtonClass: "reject-button",
        customClass: "identity-message",
      })
        .then(() => {
          let params = {
            taskId: id,
            status: type,
          };
          this.ApiService.postComputeAudit(params).then(
            (res) => {
              if (res.code === 1) {
                this.Utils.message(res.message, "warning");
                return;
              }
              this.Utils.message(res.message, "success");

              this.getTaskList();
            },
            (err) => {
              this.Utils.message(err.message, "warning");
            }
          );
        })
        .catch(() => {});
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      this.size = val;
      this.getTaskList({
        status: this.authStatus.value,
        company: this.authCompany.value,
        page: this.currentPage - 1,
        size: val,
      });
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getTaskList({
        status: this.authStatus.value,
        company: this.authCompany.value,
        page: this.currentPage - 1,
        size: this.size,
      });
    },
    getTaskList(param) {
      let params = param || {
        status: this.authStatus.value,
        company: this.authCompany.value,
        page: this.currentPage - 1,
        size: this.size,
      };
      this.ApiService.getTaskList(params).then(
        (res) => {
          res.data.content.forEach((element) => {
            element.resultFile = element.resultFile || "--";
            element.taskPort = element.taskPort || "--";
            if (element.status === "waiting") {
              element.status_fmt = "等待加入";
            }
            if (element.status === "accepted") {
              element.status_fmt = "计算中";
            }
            if (element.status === "finished") {
              element.status_fmt = "成功";
            }
            if (element.status === "rejected") {
              element.status_fmt = "已拒绝";
            }
            if (element.status === "abort") {
              element.status_fmt = "异常";
            }
            element.createTime_fmt = this.moment(element.createTime).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          });
          this.tableData = res.data.content;
          this.total = res.data.totalElements;
          this.totalPage = res.data.totalPages;
        },
        (err) => {
          this.Utils.message("warning", err.message);
        }
      );
    },
    identify() {
      let _this = this;
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.ApiService.postAuthStart({
            authIp: this.form.ip,
            authPort: this.form.port,
          }).then(
            (res) => {
              if (res.code === 1) {
                this.Utils.message(res.message, "warning");
                return;
              }
              this.getTaskList();
              // this.startDialog = false;
              this.Utils.message("操作成功！", "success");
              this.$refs.form.resetFields();
            },
            (err) => {
              this.Utils.message(err.message, "warning");
            }
          );
        } else {
          return false;
        }
      });
    },
    getCompanyList() {
      this.ApiService.getCompanyList().then((res) => {
        res.data.forEach((item) => {
          this.authCompany.options.push({
            value: item.authId,
            label: item.name,
          });
        });
      });
    },
  },
  mounted() {
    this.getCompanyList();
    this.getTaskList();
  },
};
</script>

<style lang="less" scoped>
.identity {
  height: 100%;
  padding: 20px;
  padding-bottom: 80px;
  position: relative;
  .title {
    font-size: 14px;
    color: #333333;
    font-weight: bold;
    letter-spacing: 0;
    margin-bottom: 12px;
  }
  .search-options {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
    position: relative;
    margin-bottom: 12px;
    .status {
      margin-right: 58px;
    }
    .company {
      margin-right: 17px;
    }
    .short-button {
      width: 64px;
    }
    .handler {
      position: absolute;
      right: 0;
      .el-button {
        margin-left: 6px;
      }
      .long-button {
        width: 96px;
      }
    }
  }
}
</style>
<style lang="less">
.handler-button {
  width: 96px;
  position: relative;
  left: 5px;
  border: 1px solid #3954ff;
  .popper__arrow {
    border-bottom-color: #3954ff !important;
  }
  .el-dropdown-menu__item {
    font-size: 12px;
    font-weight: bold;
    &:hover {
      background: #ffffff !important;
      color: #3954ff;
    }
  }
}
.create-Identity {
  height: 102px;
  margin-bottom: 20px;
  padding: 20px;

  .title {
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0;
    margin-bottom: 12px;
  }
  .el-form {
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 40px;
      .el-form-item__label {
        font-size: 12px !important;
        color: #333333;
        letter-spacing: 0;
        line-height: 28px;
      }
      .el-form-item__content {
        line-height: 1;
      }
    }
    .identify-button-item {
      margin-right: 0px;
      text-align: right;
      position: absolute;
      right: 40px;
    }
  }
  .el-input {
    .el-input__inner {
      width: 309px;
      height: 28px;
      color: #333333;
      background: #ffffff;
      border: 1px solid #ebeef9;
      border-radius: 2px;
      border-radius: 2px;
    }
  }
}
.identity {
  .identify-pass {
    font-size: 12px;
    color: #3145d1;
    letter-spacing: 0;
    cursor: pointer;
    margin-right: 5px;
  }
  .identify-reject {
    font-size: 12px;
    letter-spacing: 0;
    cursor: pointer;
  }
  .identify-disable {
    opacity: 0.3;
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
    line-height: 12px;
    pointer-events: none;
    cursor: not-allowed;
  }
  .table-tr {
    height: 48px;
    td {
      padding: 0px;
      font-size: 12px;
      color: #333333;
      letter-spacing: 0;
      .cell {
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
      }
    }
  }
  .table-header {
    height: 32px;
    th {
      height: 32px;
      font-size: 12px;
      color: #333333;
      letter-spacing: 0;
      font-weight: bold;
      padding: 0;
      background: #fbfcfd;
    }
  }
  .el-table th.gutter {
    display: table-cell !important;
  }
  .el-table colgroup.gutter {
    display: table-cell !important;
  }
  .el-table-column--selection .cell {
    padding-right: 10px;
  }
  .identity-pagination {
    position: absolute;
    bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100% - 40px);
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
    line-height: 12px;
    .right-part {
      display: flex;
      align-items: center;
      .el-pagination__total {
        float: left;
      }
      .el-pagination__sizes {
        float: left;
      }
      .el-pager {
        li {
          font-size: 12px;
        }
      }
    }
    .page-size-select {
      width: 93px;
      height: 24px;
      .el-input__inner {
        width: 93px;
        height: 24px;
        padding-right: 10px;
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
        background: #fbfcfd;
        border: 1px solid #ebeef9;
        border-radius: 2px;
        border-radius: 2px;
      }
    }
  }
  .status-waiting {
    div {
      color: #222222;
    }
  }
  .status-waiting {
    div {
      color: #e02020 !important;
    }
  }
  .status-rejected {
    div {
      color: #999999 !important;
    }
  }
  .status-finished {
    div {
      color: #1ea92b !important;
    }
  }
  .status-accepted {
    div {
      color: #3954ff !important;
    }
  }
}
</style>