<template>
  <div class="start-compute bgfff">
    <h2 class="title">
      <i class="el-icon-arrow-left" @click="$router.go(-1)"></i>
      发起计算
    </h2>
    <div class="start-compute-content">
      <div>
        计算合作方：
        <el-select
          v-model="companyList.value"
          @change="changeCompany"
          class="company-select"
          placeholder="请选择"
        >
          <el-option
            v-for="item in companyList.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>

      <div>
        计算合作方匹配项：
        <span class="lable-item" v-if="!loading">
          <el-radio v-model="radio" v-for="label in labels" :label="label" :key="label">
            <a :title="label" class="overflow-hidden">{{label}}</a>
          </el-radio>
        </span>
        <span v-else>
          <i class="el-icon-loading"></i>
        </span>
      </div>
      <div>
        计算合作方标签项：
        <span class="lable-item" v-if="!loading">
          <el-checkbox-group v-model="checked">
            <el-checkbox v-for="label in labels" :key="label" :label="label">
              <a :title="label" class="overflow-hidden">{{label}}</a>
            </el-checkbox>
          </el-checkbox-group>
        </span>
        <span v-else>
          <i class="el-icon-loading"></i>
        </span>
      </div>

      <div>
        计算数据选择：
        <div>
          <el-radio v-model="computeData" label="text">读取文本文件：</el-radio>
          <el-select v-model="inputList.textValue" placeholder="请选择">
            <el-option
              v-for="item in inputList.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div>
          <el-radio v-model="computeData" label="history">读取数据库记录：</el-radio>
          <el-select v-model="inputList.dataBaseValue" placeholder="请选择">
            <el-option
              v-for="item in inputList.inputDb"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
      </div>
    </div>
    <div class="start-compute-footor">
      <el-button class="normal-button" @click="$router.go(-1)">取消</el-button>
      <el-button @click="postComputed">发起任务</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      radio: '',
      checked: [],
      computeData: 'text',
      loading: true,
      companyList: {
        value: '',

        options: []
      },
      inputList: {
        dataBaseValue: '',
        textValue: '',
        options: [],
        inputDb: []
      },
      labels: []
    }
  },
  methods: {
    changeCompany() {
      this.postAuthLabel()
    },
    async getCompanyList() {
      this.loading = true
      await this.ApiService.getCompanyList({
        status: 'accepted'
      }).then(res => {
        res.data.forEach(item => {
          this.companyList.options.push({
            value: item.authId,
            label: item.name
          })
        })
        this.companyList.value = res.data[0] && res.data[0].authId
        this.loading = false
      })
      this.postAuthLabel()
    },
    postAuthLabel() {
      let params = {
        authId: this.companyList.value
      }
      this.ApiService.postAuthLabel(params).then(res => {
        this.labels = res.data.split(',')
      })
    },
    postComputed() {
      let param = {
        authId: this.companyList.value,
        dataSourceId:
          this.computeData === 'text'
            ? this.inputList.textValue
            : this.inputList.dataBaseValue,
        selectedLabels: [
          this.radio,
          ...this.checked.filter(item => this.radio !== item)
        ].join(',')
      }
      if (!param.dataSourceId) {
        this.Utils.message('请选择数据源！', 'warning')
        return
      }
      if (!param.selectedLabels) {
        this.Utils.message('请选择标签项获匹配项！', 'warning')
        return
      }
      this.ApiService.startComputed(param).then(res => {
        if (res.code === 0) {
          this.Utils.message('操作成功！', 'success')
          this.$router.go(-1)
        } else {
          this.Utils.message(res.message, 'warning')
        }
      })
    },
    getConfig() {
      this.ApiService.getConfig().then(res => {
        const fileInfo = res.data.inputConfig
        if (fileInfo.inputFile) {
          this.inputList.options.push({
            value: fileInfo.inputFile.id,
            label: fileInfo.inputFile.tableName
          })
        }
        if (fileInfo.inputDb) {
          this.inputList.inputDb.push({
            value: fileInfo.inputDb.id,
            label: fileInfo.inputDb.tableName
          })
        }
      })
    }
  },
  mounted() {
    this.getCompanyList()
    this.getConfig()
  }
}
</script>

<style lang="less" scoped>
.start-compute {
  height: 100%;
  width: 100%;
  min-height: 300px;
  position: relative;
  .title {
    i {
      cursor: pointer;
    }
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-bottom: 1px solid #ebeef9;
    font-size: 14px;
    color: #333333;
    letter-spacing: 0;
    font-weight: bold;
  }
  .start-compute-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    min-height: 500px;
    & > div {
      width: 600px;
      font-size: 12px;
      color: #333333;
      letter-spacing: 0;
      line-height: 12px;
      display: flex;
      position: relative;
      justify-content: space-between;
      i {
        position: absolute;
        left: 120px;
      }
      .lable-item {
        display: flex;
        flex-wrap: wrap;
        width: 480px;
        & > label {
          display: flex;
          align-items: center;
          margin-bottom: 5px;
        }
        .overflow-hidden {
          width: 105px;
          height: 100%;
          line-height: 18px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          display: inline-block;
        }
        .el-checkbox-group {
          width: 100%;
          position: relative;
          top: -4px;
          & > label {
            line-height: 25px;
            display: inline;
            .el-checkbox__label {
              .overflow-hidden {
                line-height: 1;
                height: 100%;
                position: relative;
                top: 2px;
              }
            }
          }
        }
      }
      &:nth-of-type(1) {
        height: 60px;
        align-items: center;
        border-bottom: 1px solid #f2f5fa;
        .company-select {
          width: 480px;
          .el-input__inner {
            width: 100%;
          }
        }
      }
      &:nth-of-type(2) {
        margin-top: 30px;
      }
      &:nth-of-type(3) {
        margin-top: 38px;
        height: 106px;
      }
      &:nth-of-type(4) {
        height: 120px;
        font-weight: bold;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        flex-direction: column;
        justify-content: flex-start;
        & > div {
          label {
            width: 130px;
            margin-right: 0px;
          }
          .el-select {
            width: 400px;
          }
          margin-top: 22px;
        }
      }
    }
  }
  .start-compute-footor {
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-top: 1px solid #ebeef9;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-align: right;
    .el-button {
      width: 96px;
    }
  }
}
</style>