<template>
  <div class="task-detail bgfff">
    <h2 class="title">
      <i class="iconfont icon6icon1" @click="$router.go(-1)"></i>
      任务详情
    </h2>
    <div>
      <!-- {{model}} -->
      <el-steps
        :active="stepModel.active"
        align-center
        finish-status="success"
        class="detail_steps"
      >
        <el-step :title="stepModel.step1.title" :description="stepModel.step1.description"></el-step>
        <el-step :title="stepModel.step2.title" :description="stepModel.step2.description"></el-step>
        <el-step :title="stepModel.step3.title" :description="stepModel.step3.description"></el-step>
        <el-step
          :title="stepModel.step4.title"
          :description="stepModel.step4.description"
          class="step4"
        ></el-step>
      </el-steps>
      <div class="middle-part">
        <div>
          <div class="middle-title">计算参与方</div>
          <div class="middle-content">
            <span>
              {{model.serverName}}
              <span>{{model.status_fmt}}</span>
            </span>
            <span>
              {{model.clientName}}
              <span>{{model.status_fmt}}</span>
            </span>
          </div>
        </div>
        <div>
          <div class="middle-title">计算耗时</div>
          <div class="spend-time">
            <i class="iconfont icon8icon3"></i>
            <h1>已耗时</h1>
            <div class="show-time">
              <span class="timeNum">{{getTimeElement.h}}</span>
              <span>h</span>
              <span class="timeNum">{{getTimeElement.m}}</span>
              <span>m</span>
              <span class="timeNum">{{getTimeElement.s}}</span>
              <span>s</span>
            </div>
          </div>
        </div>
      </div>

      <div class="bottom-part">
        <div>
          <div>
            <span>发起方</span>
            <span>{{model.clientName}}</span>
          </div>
          <div>
            <span>合作方</span>
            <span>{{model.serverName}}</span>
          </div>
          <div>
            <span>结果保存方</span>
            <span>{{model.clientName}}</span>
          </div>
          <div>
            <span>查询字段</span>
            <span>{{model.selectedLabels}}</span>
          </div>
        </div>
        <div>
          <div>
            <span>查询条数/参与计算总量</span>
            <span>{{model.receiverCount+"/"+(model.senderCount||"--")}}</span>
          </div>
          <div>
            <span>查询率</span>
            <span>{{(model.resultCount&&Number(model.resultCount/model.receiverCount*100).toFixed(2)+"%")||"--"}}</span>
          </div>
          <div>
            <span>查询耗时</span>
            <span class="show-time">
              <span class="timeNum">{{getTimeElement.h}}</span>
              <span>h</span>
              <span class="timeNum">{{getTimeElement.m}}</span>
              <span>m</span>
              <span class="timeNum">{{getTimeElement.s}}</span>
              <span>s</span>
            </span>
          </div>
          <div>
            <span>查询结果文件/查询结果表名</span>
            <span>{{model.resultFile||"--"}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {}
    }
  },
  computed: {
    stepModel() {
      switch (this.model.status) {
        case 'accepted':
          return {
            active: 3,
            step1: {
              title: '发起计算',
              description: this.moment(this.model.createTime).format(
                'YYYY-MM-DD HH:mm:ss'
              )
            },
            step2: {
              title: '申请同意',
              description:
                this.model.acceptTime &&
                this.moment(this.model.acceptTime).format('YYYY-MM-DD HH:mm:ss')
            },
            step3: {
              title: '开始计算',
              description:
                this.model.initTime &&
                this.moment(this.model.initTime).format('YYYY-MM-DD HH:mm:ss')
            },
            step4: {
              title: '计算中',
              description: ''
            }
          }
          break
        case 'waiting':
          return {
            active: 1,
            step1: {
              title: '发起计算',
              description: this.moment(this.model.createTime).format(
                'YYYY-MM-DD HH:mm:ss'
              )
            },
            step2: {
              title: '申请同意',
              description: ''
            },
            step3: {
              title: '计算开始',
              description: ''
            },
            step4: {
              title: '计算中',
              description: ''
            }
          }
          break
        case 'finished':
          return {
            active: 4,
            step1: {
              title: '发起计算',
              description: this.moment(this.model.createTime).format(
                'YYYY-MM-DD HH:mm:ss'
              )
            },
            step2: {
              title: '申请同意',
              description:
                this.model.acceptTime &&
                this.moment(this.model.acceptTime).format('YYYY-MM-DD HH:mm:ss')
            },
            step3: {
              title: '开始计算',
              description:
                this.model.initTime &&
                this.moment(this.model.initTime).format('YYYY-MM-DD HH:mm:ss')
            },
            step4: {
              title: '计算完成',
              description:
                this.model.finishedTime &&
                this.moment(this.model.finishedTime).format(
                  'YYYY-MM-DD HH:mm:ss'
                )
            }
          }
          break
        default:
          return {
            step1: {},
            step2: {},
            step3: {},
            step4: {}
          }
          break
      }
    },
    getTimeElement() {
      let endTime
      if (this.model.finishedTime) {
        endTime = this.model.finishedTime
      } else {
        endTime = new Date().getTime()
      }
      if (!this.model.initTime) {
        return {
          h: 0,
          m: 0,
          s: 0
        }
      }
      const sec = (endTime - this.model.initTime) / 1000 || 0
      const h = Math.floor(Number(sec) / 3600)
      const m = Math.floor((Number(sec) - h * 3600) / 60)
      const s = Math.ceil(Number(sec) % 60)
      return {
        h: h,
        m: m,
        s: s
      }
    }
  },
  methods: {
    getDetail() {
      const taskId = this.$route.params.taskId
      if (!taskId) {
        return
      }
      this.ApiService.getTaskDetail({
        taskId: taskId
      }).then(res => {
        if (res.data.status === 'accepted') {
          res.data.status_fmt = '计算中'
        }
        if (res.data.status === 'waiting') {
          res.data.status_fmt = '等待加入'
        }
        if (res.data.status === 'finished') {
          res.data.status_fmt = '已完成'
        }
        if (res.data.status === 'rejected') {
          res.data.status_fmt = '已拒绝'
        }

        this.model = res.data
      })
    }
  },
  mounted() {
    this.getDetail()
  },
  destroyed() {}
}
</script>

<style lang="less" scoped>
.task-detail {
  height: 100%;
  width: 100%;
  min-height: 400px;
  position: relative;
  .title {
    i {
      color: #a8affc;
      font-size: 12px;
      cursor: pointer;
    }
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-bottom: 1px solid #ebeef9;
    font-size: 14px;
    color: #333333;
    letter-spacing: 0;
    font-weight: bold;
  }
  .middle-part {
    height: 180px;
    width: 100%;
    margin-top: 42px;
    margin-bottom: 20px;
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
    & > div {
      width: 49%;
      height: 100%;
      border: 1px solid #ebeef9;
      border-radius: 5px;
      .middle-title {
        height: 36px;
        line-height: 36px;
        width: 100%;
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
        font-weight: bold;
        padding-left: 21px;
        border-bottom: 1px solid #ebeef9;
        position: relative;
        &::before {
          content: '';
          display: block;
          position: absolute;
          height: 4px;
          width: 4px;
          background: #3954ff;
          border-radius: 50%;
          left: 10px;
          top: 16px;
        }
      }
      .middle-content {
        padding: 16px;
        & > span {
          display: block;
          background: #fbfcfd;
          border: 1px solid #ebeef9;
          border-radius: 5px;
          border-radius: 5px;
          margin-bottom: 20px;
          height: 30px;
          width: 155px;
          display: flex;
          align-items: center;
          padding: 0 5px;
          justify-content: space-between;
          font-size: 12px;
          color: #333333;
          span {
            font-size: 12px;
            color: #a7afff;
            letter-spacing: 0;
            line-height: 12px;
          }
        }
      }
      .spend-time {
        padding: 16px;
        position: relative;
        margin-top: 20px;
        h1 {
          font-size: 14px;
          color: #010953;
          letter-spacing: 0;
          text-align: center;
          font-weight: bold;
          position: relative;
          z-index: 1;
        }
        div {
          text-align: center;
          margin-top: 18px;
          position: relative;
          z-index: 1;
        }
        i {
          position: absolute;
          top: -12px;
          font-size: 120px;
          color: #fbfcfd;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
  .bottom-part {
    padding: 20px;
    & > div {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      & > div {
        height: 120px;
        width: 25%;
        margin-right: 16px;
        background: #fbfcfd;
        border: 1px solid #ebeef9;
        border-radius: 5px;
        border-radius: 5px;
        padding: 22px 27px;
        &:nth-of-type(4) {
          margin-right: 0px;
        }
        & > span {
          &:nth-of-type(1) {
            font-size: 14px;
            color: #010953;
            font-weight: bold;
            letter-spacing: 0;
            display: block;
            margin-bottom: 22px;
          }
          &:nth-of-type(2) {
            font-family: Helvetica;
            font-size: 26px;
            color: #3145d1;
            letter-spacing: 0.93px;
            line-height: 32px;
            width: 100%;
            display: inline-block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  }
  .show-time {
    span {
      font-size: 14px;
      color: #3145d1;
    }
    .timeNum {
      font-weight: bold;
      font-size: 26px;
      color: #3145d1;
      letter-spacing: 0.93px;
      line-height: 32px;
      margin: 5px;
    }
  }
}
</style>
<style lang="less">
.detail_steps {
  margin-top: 65.6px;
  .el-step {
    .el-step__line {
      background-color: #ffffff;
    }
    .el-step__line-inner {
      display: none;
    }
    .el-step__head {
      .el-step__icon {
        width: 16px;
        height: 16px;
        border: 1px solid #3954ff;
      }
    }
    .is-success {
      .el-step__icon {
        background: #ebedff;
        .el-step__icon-inner {
          font-size: 10px;
          color: #3954ff;
        }
      }
    }
    .el-step__line {
      border: 1px dashed #3953ff;
    }
    .is-process {
      .el-step__line {
        border: 1px dashed #e6e6e6;
      }
      .el-step__icon {
        width: 32px;
        height: 32px;
        background: #ebedff;
        border: none;
        position: relative;
        top: -3px;
        .el-step__icon-inner {
          visibility: hidden;
        }
        &::after {
          content: '';
          display: block;
          width: 24px;
          height: 24px;
          border-radius: 50%;
          background: #ffffff;
          border: 6px solid #3954ff;
          position: absolute;
          flex-shrink: 0;
        }
      }
    }
    .is-wait {
      .el-step__line {
        border: 1px dashed #e6e6e6;
      }
      .el-step__icon {
        background: #f7f7f7;
        border: 1px solid #dcdcdc;
        .el-step__icon-inner {
          visibility: hidden;
        }
      }
    }
    .el-step__main {
      .el-step__title {
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
        text-align: center;
        line-height: 12px;
        font-weight: bold;
        margin-bottom: 10px;
        margin-top: 20px;
      }
      .el-step__description {
        font-size: 12px;
        color: #c5c8da;
        letter-spacing: 0;
        text-align: center;
        line-height: 12px;
      }
      .is-wait {
        color: #c0c4cc;
      }
      .is-process {
        margin-top: 10px;
      }
    }
  }
}
</style>